**/
.u-position-absolute {
  position: absolute;
}

.u-position-relative {
  position: relative;
}

.accordion {
  /*border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 2px;*/
  margin-left: 10px;
}

.accordion__item + .accordion__item {
  /*border-top: 1px solid rgba(0, 0, 0, 0.1);*/
}

.accordion__item--has-icon {
  position: relative;
}

.accordion__title {
  background-color: #f4f4f4;
  color: #444;
  cursor: pointer;
  padding: 18px;
  width: 100%;
  text-align: left;
  border: none;
}



.accordion__title:hover {
  background-color: #ddd;
}

.accordion__body {
  padding-left: 30px;
 /*padding-right: 20px;*/
  display: block;
  -webkit-animation: fadein 0.1s ease-in;
          animation: fadein 0.1s ease-in;
 /* border-left: 1px solid rgb(148, 146, 146);*/
}

.accordion__body--hidden {
  display: none;
  opacity: 0;
  -webkit-animation: fadein 0.1s ease-in;
          animation: fadein 0.1s ease-in;
}

.accordion__title > *:last-child,
.accordion__body > *:last-child {
  margin-bottom: 0;
}

.accordion__arrow {
  display: inline-block;
  position: relative;
  width: 24px;
  height: 12px;
  position: absolute;
  top: 50%;
  right: 0;
  margin-top: -6px;
}

.accordion__arrow::after,
.accordion__arrow::before {
  display: block;
  position: absolute;
  top: 50%;
  width: 10px;
  height: 2px;
  background-color: currentColor;
  content: '';
}

.accordion__arrow::before {
  left: 4px;
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}

[aria-expanded='true'] .accordion__arrow::before {
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.accordion__arrow::after {
  right: 4px;
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

[aria-expanded='true'] .accordion__arrow::after {
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}

.accordion__arrow::before,
.accordion__arrow::after {
  transition: transform 0.1s ease, -webkit-transform 0.1s ease;
}

/* -------------------------------------------------- */
/* ---------------- Animation part ------------------ */
/* -------------------------------------------------- */

@-webkit-keyframes fadein {
  0% {
      opacity: 0;
  }

  100% {
      opacity: 1;
  }
}

@keyframes fadein {
  0% {
      opacity: 0;
  }

  100% {
      opacity: 1;
  }
}

@-webkit-keyframes move-down {
  0% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
  }
  10% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
  }
  20% {
      -webkit-transform: translateY(5px);
              transform: translateY(5px);
  }
  30% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
  }
  100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
  }
}

@keyframes move-down {
  0% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
  }
  10% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
  }
  20% {
      -webkit-transform: translateY(5px);
              transform: translateY(5px);
  }
  30% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
  }
  100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
  }
}

@-webkit-keyframes move-up {
  0% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
  }
  10% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
  }
  20% {
      -webkit-transform: translateY(-5px);
              transform: translateY(-5px);
  }
  30% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
  }
  100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
  }
}

@keyframes move-up {
  0% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
  }
  10% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
  }
  20% {
      -webkit-transform: translateY(-5px);
              transform: translateY(-5px);
  }
  30% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
  }
  100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
  }
}

.accordion__title--animated:hover .accordion__arrow {
  -webkit-animation-name: move-down;
          animation-name: move-down;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
}

.accordion__title--animated[aria-expanded='true']:hover .accordion__arrow {
  -webkit-animation-name: move-up;
          animation-name: move-up;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
}




